@-webkit-keyframes notification {
    0% {
        right: -20px;
    }
    40% {
        right: 30px;
    }
    70% {
        right: 15px;
    }
    100% {
        right: 20px;
    }
}

@keyframes notification {
    0% {
        right: -20px;
    }
    40% {
        right: 30px;
    }
    70% {
        right: 15px;
    }
    100% {
        right: 20px;
    }
}
