body {
  --accent-color: #0071EA;
  --background-color: #ffffff;
  --text-color: #666666;
  --border-color: var(--accent-color);
  --button-text-color: #000000;
  --transition-delay: 1s;
  --title-text-color: #333333;
  --image-shadow: rgb(243, 243, 243);
  --button-box-shadow: rgba(0,0,0,.22);
  --filter: invert(0);

  text-align: center;
  background-color: var(--background-color);
  border-color: var(--border-color);
  color: var(--text-color);
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 1.75em;
  text-align: center;
  box-sizing: border-box;
  transition: 1s;
}

body.dark {
